import { ComponentProps, useContext } from 'react';
import styled from 'styled-components';
import { MetaMaskContext, MetamaskState } from '../hooks';
import { ReactComponent as FlaskFox } from '../assets/flask_fox.svg';
import {  shouldDisplayReconnectButton } from '../utils';
import { sendNotify ,stopNotify} from '../utils/snap';
const Link = styled.a`
  display: flex;
  align-self: flex-start;
  align-items: center;
  justify-content: center;
  font-size: ${(props) => props.theme.fontSizes.small};
  border-radius: ${(props) => props.theme.radii.button};
  border: 1px solid ${(props) => props.theme.colors.background.inverse};
  background-color: ${(props) => props.theme.colors.background.inverse};
  color: ${(props) => props.theme.colors.text.inverse};
  text-decoration: none;
  font-weight: bold;
  padding: 1rem;
  cursor: pointer;
  transition: all 0.2s ease-in-out;

  &:hover {
    background-color: transparent;
    border: 1px solid ${(props) => props.theme.colors.background.inverse};
    color: ${(props) => props.theme.colors.text.default};
  }

  ${({ theme }) => theme.mediaQueries.small} {
    width: 100%;
    box-sizing: border-box;
  }
`;

const Button = styled.button`
  display: flex;
  align-self: flex-start;
  align-items: center;
  justify-content: center;
  margin-top: auto;
  ${({ theme }) => theme.mediaQueries.small} {
    width: 100%;
  }
`;

const ButtonText = styled.span`
  margin-left: 1rem;
`;

const ConnectedContainer = styled.div`
  display: flex;
  align-self: flex-start;
  align-items: center;
  justify-content: center;
  font-size: ${(props) => props.theme.fontSizes.small};
  border-radius: ${(props) => props.theme.radii.button};
  border: 1px solid ${(props) => props.theme.colors.background.inverse};
  background-color: ${(props) => props.theme.colors.background.inverse};
  color: ${(props) => props.theme.colors.text.inverse};
  font-weight: bold;
  padding: 1.2rem;
`;

const ConnectedIndicator = styled.div`
  content: ' ';
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: green;
`;

export const InstallFlaskButton = () => (
  <Link href="https://metamask.io/flask/" target="_blank">
    <FlaskFox />
    <ButtonText>Install MetaMask Flask</ButtonText>
  </Link>
);

export const RecoverySnap = (props: ComponentProps<typeof Button>) => (
  <Button {...props}>
    <FlaskFox />
    <ButtonText>Recover Key</ButtonText>
  </Button>
);

export const NotifySnap = (props: ComponentProps<typeof Button>) => (
  <Button {...props}>
    <FlaskFox />
    <ButtonText>Snap Alert</ButtonText>
  </Button>
);

export const GetNotificationButton = (props: ComponentProps<typeof Button>) => {
  const state = useContext(MetaMaskContext);

  return (
    <Button {...props}
      onClick={
        ()=>{
          if(state[2]){
            stopNotify()
            state[3](false);
          }
          else{
            sendNotify()
            state[3](true);
          }
        }
      }
    >
      <FlaskFox />
      {
        !state[2] ?
        <ButtonText
        >
          Get Notifications
        </ButtonText>
        :
        <ButtonText>
          Turn off Notifications
        </ButtonText>
      }
    </Button>
  );
};

export const SetupRecoveryKeyButton = (props: ComponentProps<typeof Button>) => {

  return(
    <Button {...props}>
      <ButtonText>Setup Recovery Key</ButtonText>
    </Button>
  )
};

export const RecoverPrivateKeyButton = (props: ComponentProps<typeof Button>) => {

  return(
    <Button {...props}>
      <ButtonText>Reveal Private Key</ButtonText>
    </Button>
  )
}

export const ConnectButton = (props: ComponentProps<typeof Button>) => {
  return (
    <Button {...props}>
      <FlaskFox />
      <ButtonText>Connect</ButtonText>
    </Button>
  );
};

export const ReconnectButton = (props: ComponentProps<typeof Button>) => {
  return (
    <Button {...props}>
      <FlaskFox />
      <ButtonText>Reconnect</ButtonText>
    </Button>
  );
};

export const HeaderButtons = ({
  state,
  onConnectClick,
}: {
  state: MetamaskState;
  onConnectClick(): unknown;
}) => {
  if (!state.isFlask && !state.installedSnap) {
    return <InstallFlaskButton />;
  }

  if (!state.installedSnap) {
    return <ConnectButton onClick={onConnectClick} />;
  }

  if (shouldDisplayReconnectButton(state.installedSnap)) {
    return <ReconnectButton onClick={onConnectClick} />;
  }

  return (
    <ConnectedContainer>
      <ConnectedIndicator />
      <ButtonText>Connected</ButtonText>
    </ConnectedContainer>
  );
};
